<template>
  <div>
    <v-snackbar v-model="snackbar" top right :color="color">
      {{ msg }}
      <v-btn color="black" text @click="snackbar = false"> </v-btn>
    </v-snackbar>

    <v-form>
      <v-combobox
        v-model="user"
        :items="clientData"
        label="Send To"
        clearable
        outlined
        dense
        @change="gToken"
      ></v-combobox>

      <v-text-field v-model="title" label="Title" outlined dense> </v-text-field>

      <v-textarea outlined dense style="resize: none" v-model="message" label="Message"></v-textarea>

      <v-btn type="button" color="primary" @click="sendfcm"> Send </v-btn>
      <v-btn type="reset" outlined class="mx-2"> Reset </v-btn>
    </v-form>
  </div>
</template>
<script>
import axios from 'axios'
import { computed, onMounted, ref, reactive, defineComponent } from '@vue/composition-api'
export default defineComponent({
  setup() {
    const fcm_send = onMounted(() => {
      ap_user()
    })

    let user = ref('')
    let message = ref('')
    let token = ref('')
    let title = ref('')
    const error_ = ref(false)
    const snackbar = ref(false)
    let msg = ref('')
    let color = ref('')

    function sendfcm() {
      this.fcm_ = {
        
        // to: 'cqE4FxlfrXuFQwuqf5DbMe:APA91bGr1GGk3CQeHV3AOG3OPlervtlFc8WRU8SPT-8a0ZhVTrs6iS1nov3kXw-nzsRIy6tZubGkF0oGPsl0GbVsK-1MXmIjR3TZaVe9RcSCo4-qeUUXtoOI2qaPQFE6PxxOcja3C8Y5',
        to :token.value,
        title: title.value,
        body: message.value,
      }
      if(token.value !== '' && title.value !== '' && message.value !== ''){
      let apiURL = 'api/postAPI/send_fcm'
      axios
        .post(apiURL, this.fcm_)
        .then(res => {
          if (res) {
            user.value =''
            message.value =''
            token.value =''
            title.value =''

            snackbar.value = true
            color.value = 'success'
            msg.value = 'Notification Send successfully !'
          } else {
            snackbar.value = true
            color.value = 'error'
            msg.value = 'Something went wrong'
          }
        })
        .catch(error => {
          console.log(error)
        })
      }else{
           snackbar.value = true
            color.value = 'error'
            msg.value = 'Enter all values'
      }
    }

    const clientData = reactive([])
    function ap_user() {
      let apiURL = 'api/postAPI/loadusers'

      axios
        .get(apiURL)
        .then(res => {
          for (let i = 0; i <= res.data.length; i++) {
            clientData.push(res.data[i].Name)
          }
        })
        .catch(error => {
          console.log(error)
        })
    }
    function gToken() {
      let apiURL = `api/postAPI/getToken/${this.user}`

      axios
        .get(apiURL)
        .then(res => {
          if (res.data.length == 1) {
            token.value = res.data[0].fcmtoken
          } else {
            snackbar.value = true
            color.value = 'error'
            msg.value = 'No token found'
          }
        })
        .catch(error => {
          console.log(error)
        })
    }

    return {
      ap_user,
      fcm_send,
      sendfcm,
      user,
      message,
      token,
      title,
      clientData,
      gToken,
      error_,
      msg,
      color,
      snackbar,
    }
  },
})
</script>